import { GameContextProvider, GameContext } from "../context/GameContext"
import { useContext, useEffect, useState } from "react"
import Gallery from "react-grid-gallery"
import CheckButton from "react-grid-gallery/lib/CheckButton"
import { Fab, Action } from "react-tiny-fab"
import "react-tiny-fab/dist/styles.css"
import ScoreBoard from "./Scoreboard"
import Modal from "react-modal"

Modal.setAppElement("#root")

const IMAGES = {
  0: { guess: "dog", path: "deer_14.png", probability: 80 },
  1: { guess: "deer", path: "deer_23.png", probability: 90 },
  2: { guess: "deer", path: "deer_27.png", probability: 88 },
  3: { guess: "horse", path: "deer_24.png", probability: 99 },
  4: { guess: "cat", path: "deer_36.png", probability: 82 },
  5: { guess: "deer", path: "deer_29.png", probability: 81 },
  6: { guess: "dog", path: "deer_39.png", probability: 89 },
  7: { guess: "deer", path: "deer_30.png", probability: 98 },
  8: { guess: "horse", path: "deer_45.png", probability: 93 },
  9: { guess: "cat", path: "deer_48.png", probability: 91 },
  10: { guess: "dog", path: "deer_11.png", probability: 99 },
  11: { guess: "deer", path: "deer_2.png", probability: 93 },
  12: { guess: "deer", path: "deer_4.png", probability: 84 },
  13: { guess: "deer", path: "deer_7.png", probability: 89 },
  14: { guess: "deer", path: "deer_12.png", probability: 100 },
  15: { guess: "horse", path: "deer_13.png", probability: 99 },
}

const CORRECT_ANSWERS = [0, 1, 2, 5, 7, 11, 12, 13]
// const CORRECT_ANSWERS = Object.keys(IMAGES)
//   .filter((i) => IMAGES[i].guess === "deer")
//   .map((i) => parseInt(i))

const labelStyle = {
  padding: "5px",
  fontFamily: "Arial",
  fontSize: "75%",
  color: "white",
  background: "rgba(0,0,0,0.5)",
}

const toImage = (path, size, index, tags = () => []) => {
  const img = {
    src: `img/${path}`,
    thumbnail: `img/${path}`,
    thumbnailWidth: size,
    thumbnailHeight: size,
    index,
    correct: CORRECT_ANSWERS.includes(parseInt(index)),
    customOverlay: <SelectionsOverlay index={index} />,
  }
  img.tags = tags(img) || []
  return img
}
const toDeerImage = (index) => {
  const img = IMAGES[index]
  return toImage(img.path, 32, index, (_img) => [
    {
      value: `${img.guess} (${img.probability}%)`,
      // value: `${img.guess} (${img.probability}%) ${_img.correct ? " YES" : ""}`,
      title: `AI Jimmy Says: ${img.guess}`,
    },
  ])
}

const findYouPlayer = (gameSettings) =>
  gameSettings && gameSettings.players && gameSettings.players.length
    ? gameSettings.players[0]
    : null

const findCarnacPlayer = (gameSettings) =>
  gameSettings && gameSettings.players && gameSettings.players.length
    ? gameSettings.players[1]
    : null

const isDefined = (x) => x !== null && x !== undefined

const SelectionsOverlay = ({ index }) => {
  const [gameSettings] = useContext(GameContext)
  const _carnac = findCarnacPlayer(gameSettings)
  const _you = findYouPlayer(gameSettings)
  return (
    <div
      style={{
        color: "red",
        fontWeight: "bold",
        textAlign: "right",
        position: "absolute",
        top: 0,
        right: 0,
        backgroundColor:
          isDefined(_you.score) && !gameSettings.images[index].correct
            ? "rgba(75, 75, 75, 0.8)"
            : "rgba(75, 75, 75, 0.0)",
        width: "100%",
        height: "100%",
      }}
    >
      {_carnac.score && _carnac.selections.includes(parseInt(index)) ? (
        <div style={{ position: "absolute", top: 0, right: 0 }}>
          <CheckButton color={_carnac.color} isSelected={true} />
        </div>
      ) : null}
    </div>
  )
}

const Choices = () => {
  const [gameSettings, setGameSettings] = useContext(GameContext)

  useEffect(() => {
    setGameSettings(resetGame(gameSettings)) // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onSelectImage = (index) => {
    const updated = { ...gameSettings }
    const you = findYouPlayer(updated)
    if (you.score) return
    var img = updated.images[index]
    if (img.hasOwnProperty("isSelected")) img.isSelected = !img.isSelected
    else img.isSelected = true
    you.selections = updated.images
      .map((x, index) => ({ ...x, index }))
      .filter((x) => x.isSelected)
      .map((x) => x.index)
    setGameSettings(updated)
  }

  const you = findYouPlayer(gameSettings)
  return (
    <div className={!you.score ? "" : "scored"}>
      <Gallery
        showLightboxThumbnails={true}
        images={gameSettings.images}
        enableImageSelection={!you.score}
        onSelectImage={onSelectImage}
        onClickThumbnail={onSelectImage}
        enableLightbox={false}
        tagStyle={labelStyle}
      />
    </div>
  )
}

const resetGame = (gameSettings) => {
  const updated = { ...gameSettings }
  updated.images = Object.keys(IMAGES).map(toDeerImage)
  const _carnac = findCarnacPlayer(updated)
  const _you = findYouPlayer(updated)
  _carnac.score = null
  _you.score = null
  _you.selections = []
  return updated
}

const calculateScore = (selections) => {
  const _selections = selections || []
  const wrongAnswers = _selections.filter((x) => !CORRECT_ANSWERS.includes(x))
  const omittedAnswers = CORRECT_ANSWERS.filter((x) => !_selections.includes(x))

  return (
    Object.keys(IMAGES).length - wrongAnswers.length - omittedAnswers.length
  )
}

const ChallengeCarnac = ({ onClick }) => {
  const [gameSettings] = useContext(GameContext)
  const selectionCount = gameSettings.players[0].selections.length
  const playGame = selectionCount > 0
  const buttonClasses = playGame ? "game" : "game disabled"
  return (
    <>
      <p>
        AI Jimmy has seen a lot of deer images before, and he is really good at
        identifying a deer when he sees one.
      </p>
      <p>
        Can you tell which of the following deer images AI Jimmy may have seen?
        There is at least one.
      </p>
      <p>Select them and then hit the "Challenge Carnac" button (below).</p>
      <p>
        You will be playing against Carnac and whomever gets more correct, wins!
      </p>
      <p style={{ textAlign: "center", fontWeight: "bold" }}>
        {selectionCount > 0
          ? `${selectionCount} picture(s) selected`
          : "1 or more selections required to play"}
      </p>
      <button
        style={{ width: "100%" }}
        onClick={onClick}
        disabled={selectionCount === 0}
        className={buttonClasses}
      >
        Challenge Carnac!
      </button>
    </>
  )
}

const Information = () => {
  const [gameSettings, setGameSettings] = useContext(GameContext)
  const carnac = findCarnacPlayer(gameSettings)

  const loadScores = () => {
    const updated = { ...gameSettings }
    const _carnac = findCarnacPlayer(updated)
    const _you = findYouPlayer(updated)
    _carnac.score = calculateScore(_carnac.selections)
    _you.score = calculateScore(_you.selections)
    setGameSettings(updated)
  }
  return (
    <div style={{ marginLeft: 10 }}>
      {carnac.score ? <Results /> : <ChallengeCarnac onClick={loadScores} />}
    </div>
  )
}

const Results = () => {
  const [gameSettings, setGameSettings] = useContext(GameContext)
  const carnac = findCarnacPlayer(gameSettings)
  const you = findYouPlayer(gameSettings)
  const playAgain = () => setGameSettings(resetGame(gameSettings))
  const styles = {
    wide: {
      width: "100%",
      alignItems: "center",
      justifyContent: "center",
      display: "flex",
      flexDirection: "column",
    },
  }
  const winner = () =>
    you.score > carnac.score ? you : you.score < carnac.score ? carnac : null
  const winnerMessage = () => {
    const _winner = winner()
    return _winner
      ? `The winner is ${_winner.name}!`
      : `${you.name} tied ${carnac.name}!`
  }
  return (
    <div style={styles.wide}>
      <ScoreBoard
        home={you}
        away={carnac}
        bottomDisplay={winnerMessage}
        totalPossible={16}
      />
      <button onClick={playAgain} style={styles.wide} className="game">
        Play Again!
      </button>
    </div>
  )
}

const PageHeader = ({ title, subtitle }) => {
  const styles = {
    container: {
      marginBottom: 5,
    },
    title: {
      fontSize: 20,
      fontWeight: "bold",
    },
    subtitle: {
      fontSize: 14,
      color: "gray",
    },
  }
  return (
    <div style={styles.container}>
      <div style={styles.title}>{title}</div>
      <span style={styles.subtitle}>{subtitle}</span>
    </div>
  )
}

const Game = () => {
  const [isModalOpen, setModalOpen] = useState(false)
  const toggleModal = () => setModalOpen(!isModalOpen)
  return (
    <GameContextProvider>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <div style={{ display: "flex", flexDirection: "row" }}>
          <div style={{ minWidth: 710, maxWidth: 710 }}>
            <Choices />
          </div>
          <div style={{ flexGrow: 1 }}>
            <Information />
          </div>
        </div>
      </div>
      <Modal
        isOpen={isModalOpen}
        onRequestClose={toggleModal}
        contentLabel="My dialog"
      >
        <PageHeader
          title="A game of 16 questionsâ€¦beat Carnac!"
          subtitle="AI Jimmy is really good at identifying the object in images that he
            has seen before. Select (check) which of the following images you
            think Jimmy remembers. You will be playing against Carnac and who
            ever gets more correct, wins!"
        />
        <button
          onClick={toggleModal}
          style={{
            position: "absolute",
            top: 5,
            right: 5,
            border: 0,
            background: "none",
          }}
        >
          <CheckButton color="black" isSelected={true} />
        </button>
      </Modal>
      <Fab
        alwaysShowTitle={true}
        mainButtonStyles={{
          backgroundColor: "#00b5ad",
        }}
        icon=". . ."
      >
        <Action text="Info" onClick={toggleModal}>
          i
        </Action>
      </Fab>
    </GameContextProvider>
  )
}

export default Game
