const ScoreBoard = ({
  home,
  away,
  bottomDisplay = () => "",
  totalPossible = 0,
}) => {
  const styles = {
    container: {
      marginTop: 2,
      backgroundColor: "#444",
      color: "white",
      display: "flex",
      flexDirection: "column",
      fontFamily: "Helvetica Neue",
      minWidth: 500,
      maxWidth: 500,
      width: 500,
    },
    scoreboard: {
      flexDirection: "row",
      margin: 6,
      border: "5px solid white",
      borderRadius: 10,
    },
    sideContainer: {
      display: "flex",
      flexDirection: "row",
    },
    side: {
      flex: 1,
      textAlign: "center",
    },
    team: {
      padding: 10,
      textAlign: "center",
      fontSize: 32,
      borderBottom: "5px solid white",
      margin: 0,
      fontWeight: "bold",
    },
    scoreContainer: { padding: 10 },
    score: { fontSize: 64, fontFamily: "Impact, sans-serif" },
    scoreDivider: { fontSize: 64 },
    bottom: {
      fontSize: 18,
      padding: 5,
      borderTop: "5px solid white",
      fontWeight: "bold",
      textAlign: "center",
    },
  }

  const ScoreboardSide = ({ player, style = {} }) => {
    const scoreDisplay =
      totalPossible > 0 ? `${player.score} / ${totalPossible}` : player.score
    const percentageDisplay =
      totalPossible > 0 ? (
        <div>({(player.score / totalPossible) * 100}%)</div>
      ) : null
    return (
      <div style={{ ...style, ...styles.side }}>
        <div style={{ ...styles.team, color: player.color }}>{player.name}</div>
        <div style={styles.scoreContainer}>
          <div style={styles.score}>{scoreDisplay}</div>
          {percentageDisplay}
        </div>
      </div>
    )
  }

  const bottom = bottomDisplay()

  return (
    <div style={styles.container}>
      <div style={styles.scoreboard}>
        <div style={styles.sideContainer}>
          <ScoreboardSide player={home} />
          <ScoreboardSide
            player={away}
            style={{ borderLeft: "3px solid white" }}
          />
        </div>
        {bottom ? <div style={styles.bottom}>{bottom}</div> : null}
      </div>
    </div>
  )
}

export default ScoreBoard
